import { TranslateService } from '@ngx-translate/core';
import { LanguageService } from './../../../shared/services/language/language.service';
import { AlertService } from './../../../shared/services/alert/alert.service';
import { ProfileService } from './../services/profile.service';
import { RouterConstants } from 'src/app/shared/config/constants/router.constants';
import { Component, OnInit } from '@angular/core';
import { Toast } from '@capacitor/toast';

@Component({
  selector: 'app-configuration',
  templateUrl: './configuration.page.html',
  styleUrls: ['./configuration.page.scss'],
})
export class ConfigurationPage implements OnInit {

  RouterConstants = RouterConstants;
  daysWeek: Array<any>;
  hoursDay: Array<string>;
  days: Array<number>;
  hours: Array<string>;
  comments: string;
  loading: boolean;
  constructor(
    private profileService: ProfileService,
    private alertService: AlertService,
    private languageService: LanguageService,
    private translate: TranslateService,
  ) { }

  ngOnInit() {
    this.hoursDay = ['07:30', '08:00', '08:30', '09:00', '09:30', '10:00', '10:30', '11:00', '11:30', '12:00', '12:30', '13:00', '13:30', '14:00', '14:30', '15:00', '15:30', '16:00', '16:30', '17:00', '17:30', '18:00', '18:30', '19:00', '19:30'];
    this.comments = '';
    this.initDays();
    this.getReminders();
  }

  ionViewWillEnter() {
    this.initDays();
  }

  initDays() {
    this.daysWeek = [
      { key: 1, value: this.translate.instant('GENERIC.DAYS_WEEK.1') },
      { key: 2, value: this.translate.instant('GENERIC.DAYS_WEEK.2') },
      { key: 3, value: this.translate.instant('GENERIC.DAYS_WEEK.3') },
      { key: 4, value: this.translate.instant('GENERIC.DAYS_WEEK.4') },
      { key: 5, value: this.translate.instant('GENERIC.DAYS_WEEK.5') }
    ];
  }

  async changeLanguage() {
    const buttons = [
      { text: this.translate.instant('ALERTS.CANCEL'), role: 'cancel', },
      { text: this.translate.instant('ALERTS.ACCEPT'), role: 'accept' }
    ];
    let name1 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.SPANISH');
    let name2 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.ENGLISH');
    let name3 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.CATALAN');
    let name4 = this.translate.instant('PAGES.LOGIN.TRANSLATIONS.RUSSIAN');
    const inputs = [
      { name: name1, type: 'radio', label: name1, value: 'es', checked: (this.languageService.currentLanguage === 'es') ? true : false },
      { name: name2, type: 'radio', label: name2, value: 'en', checked: (this.languageService.currentLanguage === 'en') ? true : false },
      { name: name2, type: 'radio', label: name4, value: 'ru', checked: (this.languageService.currentLanguage === 'ru') ? true : false },
      { name: name2, type: 'radio', label: name3, value: 'ca', checked: (this.languageService.currentLanguage === 'ca') ? true : false },
    ];
    let result: any = await this.alertService.showBasicAlert(this.translate.instant('PAGES.LOGIN.TRANSLATIONS.CHANGE'),this.translate.instant('PAGES.LOGIN.TRANSLATIONS.SELECT'),buttons, inputs);
    if (result && result.data && result.role === 'accept') this.languageService.currentLanguage = result.data.values;
    this.languageService.changeLanguage(this.languageService.currentLanguage);
  }

  async sendText() {
    try{
      this.loading = true;
      await this.profileService.createComment(this.comments).toPromise();
      this.comments = '';
      this.loading = false;
      await Toast.show({text: this.translate.instant('PAGES.PROFILE.CONFIGURATION.COMMENT_SEND'), duration: 'long', position: 'bottom'})
    } catch (error) {
      console.log(error);
      this.loading = false;
    }
  }

  async getReminders() {
    try{
      this.loading = true;
      let reminders = await this.profileService.getRemindersConf().toPromise();
      console.log('Reminders', reminders);
      this.days = reminders.days;
      this.hours = reminders.hours;
      this.loading = false;
    } catch (error) {
      console.log(error);
      this.loading = false;
    }
  }

  async saveReminders() {
    try{
      this.loading = true;
      await this.profileService.saveRemindersConf(this.days, this.hours).toPromise();
      this.loading = false;
      await Toast.show({ text: this.translate.instant('PAGES.PROFILE.CONFIGURATION.REMINDERS_SAVE'), duration: 'long', position: 'bottom' });
      this.getReminders();
    } catch (error) {
      console.log(error);
      this.loading = false;
    }
  }

  selectDays(element) {
    this.days = element.detail.value;
    console.log('selectDays ',this.days);
  }

  selectHours(element) {
    this.hours = element.detail.value;
  }

}
